"use client";

import * as React from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { getServerSession } from "next-auth";
import { authOptions } from "@/app/api/auth/[...nextauth]/route";
import { Role } from "@prisma/client";

import { useAuth } from "@/hooks/use-auth";
import { cn } from "@/lib/utils";
import { Button } from "@/components/ui/button";
import {
  Sheet,
  SheetContent,
  SheetHeader,
  SheetTitle,
  SheetTrigger,
} from "@/components/ui/sheet";
import { Menu } from "lucide-react";
import { DASHBOARD_NAV_LINKS } from "@/lib/constants";

export interface MobileNavProps {
  links: Array<{
    href: string;
    title: string;
  }>;
}

export function MobileNav({ links }: MobileNavProps) {
  const [open, setOpen] = React.useState(false);
  const pathname = usePathname();
  const { status, user } = useAuth();
  
  const isAuthenticated = status === "authenticated";
  const userRole = user?.role || null;

  // Filter links based on user role
  const filteredLinks = isAuthenticated && userRole
    ? DASHBOARD_NAV_LINKS.filter((link) => link.roles.includes(userRole as Role))
    : [];

  return (
    <Sheet open={open} onOpenChange={setOpen}>
      <SheetTrigger asChild className="md:hidden">
        <Button variant="outline" size="icon" className="h-8 w-8">
          <Menu className="h-5 w-5" />
          <span className="sr-only">Toggle menu</span>
        </Button>
      </SheetTrigger>
      <SheetContent side="right" className="flex flex-col">
        <SheetHeader className="border-b pb-4">
          <SheetTitle>EduAsses</SheetTitle>
        </SheetHeader>
        <div className="flex flex-1 flex-col gap-1 pt-4">
          {links.map((link) => (
            <Button key={link.href} variant="ghost" className="justify-start" asChild>
              <Link href={link.href}>
                {link.title}
              </Link>
            </Button>
          ))}
        </div>
      </SheetContent>
    </Sheet>
  );
} 